export const CliValidationError = variant({
  InvalidDashboardPageRoute: fields<{ route: string; errorMessage: string }>(),
  InvalidEmbeddedScriptFolder: fields<{
    path: string;
    errorMessage: string;
  }>(),
  InvalidEmbeddedScriptTemplate: fields<{
    templateFilename: string;
    errorMessage: string;
  }>(),
  InvalidCustomElementTagName: fields<{
    tagName: string;
    errorMessage: string;
  }>(),
  InvalidSiteFrontendPackageVersion: fields<{
    packageName: string;
    versionRange: string;
  }>(),
  InvalidServicePluginImplementation: fields<{
    configuredImplementation: string;
  }>(),
});

export const CliErrorCode = variant({
  ...CliSystemErrorCode,
  ...CliUserErrorCode,
  ...CliValidationError,
});
